@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*, ::after, ::before{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8vh;
  padding: 5px 1rem;
  background-color: #222;
}

nav{
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  color: #fff;
}

.cta{
  width: 140px;
  height: 40px;
  padding: 2.5px 3px;
  border: 2px solid yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}
.cta a{
  text-decoration: none;
  color: yellow;
}
.cta:hover{
  background-color: #000;
}

.filters{
  position: absolute;
  top: 10vh;
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 5px;
  background-color: aliceblue;
  border-radius: 10px;
}
.filters .btn-grp{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row;
}
.filters .btn-grp button{
  min-width: 80px;
  height: auto;
  padding: 2.5px 3px;
  border: 2px solid deepskyblue;
  border-radius: inherit;
  cursor: pointer;
}
.filters .btn-grp button:hover{
  background-color: lightblue;
}

.mainBg{
  position: absolute;
  top: 20vh;
  width: 100%;
  height: auto;
  padding: 1rem;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.card{
  width: 200px;
  height: auto;
  margin: 1rem;
  border-radius: 10px;
  background-color: aqua;
  transition: all .3s ease-in-out 0;
}
.card:hover{
  box-shadow: 3px 3px 8px rgb(170, 170, 170), -3px -3px 8px rgb(170, 170, 170);
}
.card img{
  width: 200px;
  height: 125px;
  border-radius: inherit;
  object-fit: cover;
}
.card h3, .card h4{
  margin-left: 1rem;
}

@media (max-width: 768px) {
  .filters{
    flex-direction: column;
  }
}